h3{color:darkblue;font-size:24px}#loading-animation{visibility:hidden;width:100px;height:100px;border:5px solid grey;position:absolute;margin:0 auto;border-top:5px solid darkblue;border-radius:100%;animation:spin 1s infinite linear}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}#loading-animation.show{visibility:visible}svg#rectangularareachart1,svg#rectangularareachart2{position:relative;top:4px}
